<template>
    <div class="newcourse">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 作业项目
                </el-breadcrumb-item>
                <el-breadcrumb-item>新建作业项目</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container">
            <div class="form">
                <el-form ref="form" :model="form" label-width="110px">
                    <el-form-item label="作业名称："> 
                        <el-input size="small" v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="简介：">
                        <el-input  size="small" type="textarea" rows="5" v-model="form.desc"></el-input>
                    </el-form-item>
                    <el-form-item label="资料：">
                      <div class="data-out">
                        <p class="data-bg">
                          <span>车床吉林省管.png</span>
                          <i class="deldata el-icon-delete" @click="deldataone"></i>
                        </p>
                        <p class="data-bg">
                          <span>车床吉林省管.png</span>
                          <i class="deldata el-icon-delete" @click="deldataone"></i>
                        </p>
                        <el-button size="mini" @click="modifyStuList">添加资料</el-button>
                      </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="saveEdit">保 存</el-button>
                        <el-button size="small" @click="editVisible = false">取 消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="添加资料" :visible.sync="editVisible" width="500px">
            <div class="select-bg">
                <span>筛选文件夹：</span>
                <el-select v-model="value" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <el-table
                size="mini"
                ref="multipleTable"
                class="editlist"
                :data="tableData"
                tooltip-effect="dark"
                height="300px"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                  type="selection">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="全选">
                </el-table-column>
              </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            editVisible:false,
            checked:false,
            tableData: [{
              date: '2016-05-02',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1518 弄'
            }, {
              date: '2016-05-04',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1517 弄'
            }, {
              date: '2016-05-01',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1519 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }],
            options: [{
              value: '选项1',
              label: '黄金糕'
            }, {
              value: '选项2',
              label: '双皮奶'
            }, {
              value: '选项3',
              label: '蚵仔煎'
            }, {
              value: '选项4',
              label: '龙须面'
            }, {
              value: '选项5',
              label: '北京烤鸭'
            }],
            value: '',
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: true,
                type: ['步步高'],
                resource: '小天才',
                desc: '',
                options: []
            }
        };
    },

    methods: {
        handleEdit(index, row) {
            console.log(index, row);
        },
        // 删除操作
        deldataone(index, row) {
            // 二次确认删除
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        },
        modifyStuList(){
            this.editVisible = true;
        }
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .newcourse{
        .stu-list{
            border:1px solid #ccc;
        }
        .data-out{
          padding-top:10px;
        }
        .data-bg{
          font-size:12px;
          line-height:24px;
          color:#666;
          .deldata{
            cursor:pointer;
            margin-left:20px;
            color:#f56c6c;
            font-size:14px;
          }
        }
        .stunumber{
            padding:10px 0;
            color:#666;
            span{
                margin-right:40px;
            }
        }
        .editlist{
            margin-top:20px;
            border:1px solid #eee;
        }
    }
</style>


